export const ProjectLinks = styled.div`
  display: flex;
  align-items: center;
  margin: 20px 0;
  margin-bottom: 0;

  a {
    margin-right: 10px;
  }
`;

export const ProjectPreview = styled.div`
  grid-column: 2 / 4;
  grid-row: 1;
  position: relative;
  right: -20px;

  @media ${props => props.theme.media.fablet} {
    right: 0;
    grid-column: 1 / 4;
    margin-bottom: 20px;
  }

  iframe {
    border-radius: 10px;
  }
`;
export const Tags = styled.div`
  @media ${props => props.theme.media.fablet} {
    display: none;
  }

  margin: 10px;
  color: ${props => props.theme.primaryColor};
  display: flex;
  font-size: 24px;

  svg,
  i {
    margin-right: 25px;
  }
`;
